  <div id="app">
    <transition name="fade">
      <div class="buy dingding" v-if="need_update">
        <div class="buy-template dingding-template">
          <div class="close dingding-close" @click="need_update = false">×</div>
          <img class="img myimg" src="./assets/fire.png"/>
          <p class="sentance">{{update_text}}</p>
          <p class="dingding-sentance">{{update_text}}</p>
          <div class="button" @click="toUpdate">马上升级</div>
        </div>
      </div>
    </transition>
    <router-view />
    <transition name="fade">
      <div class="tips" v-if="tips.toggle">{{tips.desc}}</div>
    </transition>
