<script>
import Share from '@/js/shareAndGetName'
import BScroll from 'better-scroll'
import { callAppRouter } from '@/js/tool'
export default {
  data () {
    return {
      need_update: false,
      update_text: '',
      tips: {
        desc: '',
        toggle: false,
        busy: false
      },
      buy: {
        toggle: false,
        desc: '暂无抽奖资格',
        button_desc: '开通会员获抽奖资格'
      },
      url: 'https://api.debug.psy-1.com',
      openid: '123',
      recevier_data: {
        data: '',
        toggle: false,
        type: 1
      },
      click_once: [{ one: false }],
      user_input: {
        user_name: '',
        user_number: '',
        user_address: ''
      },
      ticket_code: '',
      loading: false
    }
  },
  methods: {
    fuck () {
      // this.buy.toggle = false
      // location.reload()
      console.log('??')
      location.reload()
    },
    toHuiYuanCenter () {
      callAppRouter('Redirect', {
        'code': 10017
      }, (res, ed) => {
        console.log(this.$route.path)
        // this.$router.go(0)
      })
    },
    bScroll () {
      const wrapper = document.querySelector('#app')
      this.better_scroll = new BScroll(wrapper, {
        click: true,
        bounce: {
          top: false,
          bottom: false,
          left: false,
          right: false
        }
      })
    },
    toMemberCenter () {
      callAppRouter('Redirect', {
        code: ''
      })
    },
    Tips (message) {
      this.tips.desc = message
      if (!this.tips.busy) {
        this.tips.toggle = true
        this.tips.busy = true
        setTimeout(() => {
          this.tips.toggle = false
          this.tips.busy = false
        }, 2000)
      }
    },
    userData (data, type = 2, code, ticketCode) {
      this.ticket_code = ticketCode
      this.recevier_data.toggle = true
      this.recevier_data.data = data
      this.recevier_data.type = type
    },
    submit () {
      if (this.click_once[0].one) {
        return
      }
      this.click_once[0].one = true
      setTimeout(() => {
        this.click_once[0].one = false
      }, 300)
      this.recevier_data.toggle = false
      const receiver = {
        user_name: this.user_input.user_name,
        user_number: this.user_input.user_number,
        user_address: this.user_input.user_address
      }
      // eslint-disable-next-line
      $.post(this.url + '/web/v1/vip/lottery/complete', {openid: this.openid, ticket_id: this.ticket_code, ticket_receiver: JSON.stringify(receiver)}, (data) => {
        console.log(data.msg)
        if (data.msg === '成功') {
          this.Tips('成功')
          this.$children[0].getRewardData()
        }
      })
    },
    update (resolve) {
      callAppRouter('getEnv', {}, (res, ed) => {
        let mesg = null
        if (typeof ed === 'string') {
          mesg = JSON.parse(ed)
        } else {
          mesg = ed
        }
        console.log(mesg)
        console.log(parseInt(mesg.platformid) === 2 && parseInt(mesg.version) <= 45)
        if (parseInt(mesg.data.platformid) === 2 && parseInt(mesg.data.version) <= 45) {
          console.log('你是安卓你需要更新')
          this.update_text = '请升级到最新版本'
          this.need_update = true
          resolve(true)
        } else {
          console.log('最新版本')
          this.need_update = false
          resolve(false)
        }
      })
    },
    toUpdate () {
      callAppRouter('updateApp')
    }
  },
  mounted () {
    // console.log(WJH)
    // this.test()
    // document.body.addEventListener('touchstart', function () {})
    // eslint-disable-next-line
    const share = new Share({ pic: 'http://wx3.sinaimg.cn/mw690/006Zdy2vgy1fxj0dtbn29j302o02nmx1.jpg', url: 'https://sleep.heartide.com/', title: '小睡眠', desc: '给你一个婴儿般好小睡眠' })
    share.appShare()
    // this.update()
  }
}
</script>

